.spns-list{
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    position:relative;
    gap: 50px;
}
.spns-container{
    width: 100px;
    height: 150px;
    padding: 0px;
    position: relative;
}
.spns-container img{
    width: 100%;
    aspect-ratio: 1;
}
.spns-container div{
    text-align: center;
    font-size: 20px;
    margin: 5px;
}

